import React, {useState} from 'react';
import PageOptions from './PageOptions';
import { StyledGridInfo } from './styles/StyledGrid';
import { Link } from 'react-router-dom';
import { useUpcomingFetch } from './hooks/useUpcomingFetch';
import {
        IMAGE_BASE_URL,
        POSTER_SIZE,
        UP_COMING_BASE_URL,
        SEARCH_BASE_URL
        } from '../config';
import Grid from './Grid';
import MovieThumdnail from './MovieThumbnail';
import SearchBar from './SearchBar';
import Spinner from './Spinner';
import NoImage from './images/no_image.jpg';
import Header from './Header';

const Upcoming = () => {

    const [{state: {movies},
            loading,
            },
             fetchmovies] = useUpcomingFetch();
        
    const [searchItem, setSearchItem] = useState(' ');

    const searchMovies = search => {

        const endpoint = search ? `${SEARCH_BASE_URL}${search}` : `${UP_COMING_BASE_URL}`; 

        setSearchItem(search);
        fetchmovies(endpoint);

    }

    return (
        <>
            <Header />
            <SearchBar callback={searchMovies}/>
            <PageOptions />
            <Grid header={searchItem ? 'Search Result' : 'Popular Movies'}>
                {
                    movies.slice(0, 12).map(movie => ( 
                        <div className="movie-display-grid">
                            <MovieThumdnail
                            key= {movie.id}
                            clickable
                            image= {movie.poster_path ? `${IMAGE_BASE_URL}${POSTER_SIZE}${movie.poster_path}`
                                    : NoImage}
                            movieId={movie.id}
                            movieName={movie.original_title} />
        
                            <div>
                            <StyledGridInfo>
                            <Link to={`/${movie.id}`}>
                                <h4>PLOT</h4>
                                <p>{movie.overview.slice(0, 50)+',,,Read more'}</p>
                                <p>{movie.release_date}</p>
                                <h3>Movie Rating</h3>
                                <div className="score">{movie.vote_average}</div>
                            </Link>
                            </StyledGridInfo>
                            </div>
                           
                        </div>
                    ))
                }
            </Grid>
            {loading && <Spinner />}
        </>
    
    );
}
 

export default Upcoming;